// Colors
$color-dark-accent: black;
$color-light-accent: white;
$color-background-teal:#1c4238;
$color-background-darkteal: #1f4444;
$color-background-lightgray: #cacacae0;

// Breakpoints
$breakpoint-small: 768px;
$breakpoint-medium: 1200px;
$breakpoint-large: 1550px;
$breakpoint4k: 2560px;
// Spacing
$section-padding: 15vh 0;

//TYPOGRAPHY
$headingfont-family: 'Open Sans Condensed', 'Bebas Neue', 'Franklin Gothic Condensed', 'Arial', sans-serif;
$bodyfont-family: 'Cormorant Garamond', 'Lora', serif;

//TYPOGRAPHIC SCALE_______________________________________________________
h1, h2, h3, .small_text { 
  margin: .5em 0;
  color: $color-light-accent; 
}  
  
h1, .small_text{
  font-family: $headingfont-family; 
  text-transform: uppercase;
}

h1, h2{
  font-weight: 700;
}

h2, h3{
  font-family: $bodyfont-family;
  text-transform: uppercase;
}

h3, .small_text{
  font-weight: 100;
  letter-spacing: .2em; 
}
// _____
h1 {
  font-size: 12vw;
  margin-bottom: 0;
  text-align: center;
  margin-top: 0;
  letter-spacing: .02em;
}

.subhead{
  margin-top:0;
  font-size: 3.2vw;
  font-family: $bodyfont-family;
  color:  $color-light-accent;
  text-transform: none;
  font-weight: 300;
  margin-bottom: 4rem;
  text-align: center;
  margin-top: -.6em;
  margin-bottom: 7vh;
  letter-spacing: .03em; 
}

h2 {
  font-size: 6vw;
  margin-bottom: 0;
  margin-top: 0;
  letter-spacing: .05em;
}

h3 {
  margin-top: 0;
  font-size: 2vw;
  color: $color-light-accent;
  font-weight: 700;
}

.small_text{
  font-size: 2vw;
  margin-bottom: 0;
}

// "save the date" format
.save_date{
margin-top: 6vh;
padding-bottom: 0;
font-style: italic;
text-transform: uppercase;
text-align: center;
font-size: 5vw;
}

.web_soon_text{
padding-top: 0;
color: $color-background-lightgray;
text-align: center;
} 

//HERO AND COMING SOON PAGE TYPE &STYLING_________________________________________________

#hero {
padding: $section-padding;
  .full-height {
    height: 100vh;
  }
}

.container{
  padding: 0 50px;
  max-width: 1600px;
  margin: 0 auto;
  display: grid;
  grid-gap: 0;
  place-items: center;
  grid-template-columns: repeat(7, 1fr);
  grid-template-areas:
    ". head head head head head ."
    ". id_invite id_invite line anim_invite anim_invite ."
    ". save_date_web save_date_web save_date_web save_date_web save_date_web .";
   
  .head {
    grid-area: head;
  }

  .id_invite {
    grid-area: id_invite;
  }

  .line{
    grid-area: line;
    height: 100%;
    border-right: 1px solid white;
  }

  .anim_invite {
    grid-area: anim_invite;
  }

  .save_date_web {
    grid-area: save_date_web;
  }
}

@media (max-width: $breakpoint-small){
  #hero{
    padding-top: 4rem;
  }
  
  .container{
    display: block;
    padding-top: 0;
    

  h2, h3, .small_text, .anim_invite, .id_invite{
    text-align: center;
  }

  h1{
    font-size: 15vw;
    margin-bottom: 0;
  }

  .subhead{
    font-size: 4vw;
  }

  .small_text{
    font-size: 1.3rem;
  }

  .anim_invite{
    margin-top: 2em;
  }
  
  // type_______
    h2{
      font-size: 4rem;
    }

    h3{
      font-size: 1.4rem;
    }

    .save_date{
      font-size: 8vw;
    }
    
    .web_soon_text{
      font-size: 4vw;
    }

    .line{
      grid-area: line;
      height: 100%;
      padding-bottom: 2vh;
      border-right: 0;
      border-bottom: 1px solid white;
    }
  }
}  

// Base (COLOR BACKGROUND)__________________________
html {
  box-sizing: border-box;
}
  
*, 
*:before, 
*:after {
  box-sizing: inherit;
}
  
body{
  background-image: url(img/background.jpg);
}
